import React, { useEffect, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import Backdrop from '@material-ui/core/Backdrop';
import Fade from '@material-ui/core/Fade';
import axios from 'axios';
import { Button } from "@material-ui/core";
import YouTubeIcon from "@material-ui/icons/YouTube";
import Carousel from '../Carousel/Carousel';
import { img_500,unavailable,unavailableLandscape} from '../Relator/Relative';
import './NodeModal.css';

const useStyles = makeStyles((theme) => ({
  modal: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  paper: {
   width : "90%",
   height : "80%",
   backgroundColor : "rgb(0,0,0)",
   border : "1px solid rgb(256,256,256)",
   borderRadius : 10,
   color : "white",
   boxShadow : theme.shadows[5],
   padding : theme.spacing(1,1,3),
  },
}));

const  NodeModal = ({children,media_type,id}) => {

  const [open,setOpen] = useState(false);
  const [content,setContent] = useState();
  const [video,setVideo] = useState(); 
  
  
  const classes = useStyles();

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const fetchNodeData = async () => {
    const { data } = await axios.get(`https://api.themoviedb.org/3/${media_type}/${id}?api_key=${process.env.REACT_APP_CENTRE_API}&language=en-US`);
        
    setContent(data);

  }


  const fetchVideo = async () => {
    const { data } = await axios.get(`https://api.themoviedb.org/3/${media_type}/${id}/videos?api_key=${process.env.REACT_APP_CENTRE_API}&language=en-US`);

    setVideo(data.results[0]?.key);
  }

  useEffect(() => {
    fetchNodeData();
    fetchVideo();
    // eslint-disable-next-line
  },[]);

  return (
    <div>
      <div className="node"
          type="button" 
          onClick={handleOpen}
          color = "inherit"
          style = {{cursor:"pointer"}}>
        {children}
      </div>
      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        className={classes.modal}
        open={open}
        onClose={handleClose}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={open}>
        {content && (
            <div className={classes.paper}>
              <div className="NodeModal">
                <img  className="NodeModal__portrait"
                  src={
                    content.poster_path
                      ? `${img_500}/${content.poster_path}`
                      : unavailable
                  }
                  alt={content.name || content.title}
                />
                <img
                  src={
                    content.backdrop_path
                      ? `${img_500}/${content.backdrop_path}`
                      : unavailableLandscape
                  }
                  alt={content.name || content.title}
                  className="NodeModal__landscape"
                />
                <div className="NodeModal__about">
                  <span className="NodeModal__title">
                    {content.name || content.title} (
                    {(
                      content.first_air_date ||
                      content.release_date ||
                      "-----"
                    ).substring(0, 4)}
                    )
                  </span>
                  {content.tagline && (
                    <i className="tagline">{content.tagline}</i>
                  )}

                  <span className="NodeModal__description">
                    {content.overview}
                  </span>

                  <div>
                    <Carousel id={id} media_type={media_type} />
                  </div>

                  <Button
                    variant="contained"
                    startIcon={<YouTubeIcon />}
                    color="secondary"
                    target="__blank"
                    href={`https://www.youtube.com/watch?v=${video}`}
                  >
                    Watch the Trailer
                  </Button>
                </div>
              </div>
            </div>
          )}
            </Fade>
      </Modal>
    </div>
  );
}

export default NodeModal;